.calories {
    width: 100%;
    margin: 355px 0 111px 0;
    @media (max-width: $md-3) {
        margin: 50px 0 30px 0;
    }
    @media (max-width: $md-5) {
        margin: 10px 0 30px 0;
    }
    // .calories__container

    &__container {
        width: 100%;
        height: 100%;
    }

    // .calories__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: $md-3) {
            flex-direction: column;
            text-align: center;
        }
    }

    // .calories__left

    &__left {
    }

    // .calories__right

    &__right {
    }

    &__block {
        @media (max-width: $md-3) {
            align-items: center;
            margin: 0 0 50px 0;
        }
    }

    // .calories__circle

    &__circle {
        background-color: $beigeColor;
        overflow: hidden;
        @media (max-width: $md-3) {
            width: 290px;
            height: 290px;
         }
    }

    // .calories__image

    &__image {
        position: relative;
        border-radius: 25px;
        transform: translate(45%, 0px);
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
         @media (max-width: $md-3) {
            transform: translate(0, 0);
            width: 150px;
         }
    }
}
