.life {
    width: 100%;
    position: relative;
    // .life__container

    &__container {
        width: 100%;
        height: 100%;
        padding: 250px 15px 245px 15px;
        @media (max-width: $md-4) {
            padding: 120px 15px 125px 15px;
        }
    }

    // .life__body

    &__body {
        width: 100%;
        height: 100%;
        font-family: "Euclid", sans-serif;
    }

    // .life__top

    &__top {
        display: flex;
        flex-direction: column;
        max-width: 513px;
        color: $mainColor;
        margin: 0 0 360px 0;
        @media (max-width: $md-3) {
			margin: 0 0 150px 0;
		}
        @media (max-width: $md-5) {
            margin: 0 0 70px 0;
        }
    }

    // .life__title

    &__title {
        font-size: 90px;
        font-weight: 600;
        margin: 0 0 36px 0;
        @media (max-width: $md-4) {
			font-size: 60px;
		}
    }

    // .life__subtitle

    &__subtitle {
        font-size: 26px;
        margin: 0 0 36px 0;
        display: flex;
        align-items: center;
        span {
            margin: 0 0 0 4px;
            position: relative;
            padding-bottom: 2px;
            top: 2px;
            border-bottom: 2px solid $yellowColor;
        }
    }

    // .life__banana

    &__banana {
        width: 44px;
        height: 44px;
        margin: 0 0 0 10px;
    }

    // .life__button

    &__button {
        @include no-btn();
        width: 186px;
        height: 81px;
        border-radius: 10px;
        color: #fff;
        font-weight: 500;
        font-size: 18px;
        cursor: pointer;
        transition: .5s;
        background-color: $mainColor;
        border: 2px solid  rgba(0, 0, 0, 0);
        &:hover {
            transition: .5s;
            color: $mainColor;
            border: 2px solid  $mainColor;
            background-color: #fff;
        }
    }

    // .life__bottom

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column-reverse;
            align-items: center;
        }
    }

    // .life__circle

    &__circle {
        overflow: hidden;
        background-color: $pinkColor;
        @media (max-width: $md-3) {
            width: 290px;
            height: 290px;
         }
    }

    // .life__image-circle

    &__image-circle {
        position: relative;
        transform: translate(45%, 30%);
        object-fit: contain;
        border-radius: 25px;
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
        &::before {
            position: absolute;
            content: "";
            z-index: 2;
            background-color: red;
        }
        @media (max-width: $md-3) {
            transform: translate(23%, 20%);
            width: 200px;
         }
         
    }

    // .life__block

    &__block {
        @media (max-width: $md-3) {
           margin:  0 0 50px 0;
           text-align: center;
           align-items: center;
        }
    }

    // .life__background

    &__background {
        position: absolute;
        top: 0;
        right: 0;
        z-index: -50;
        object-fit: cover;
        @media (max-width: $md-3) {
            width: 600px;
        }
        @media (max-width: $md-4) {
            width: 400px;
        }
    }

    // .life__background-girl

    &__background-girl {
        position: absolute;
        top: 420px;
        right: 80px;
        z-index: -49;
        @media (max-width: $md-1) {
            right: 0;
        }
        @media (max-width: $md-2) {
            display: none;
        }
    }
}

