.track {
    width: 100%;
    position: relative;
    // .track__container

    &__container {
        width: 100%;
        height: 100%;
        
    }

    // .track__body

    &__body {
        width: 100%;
        height: 100%;
        padding: 0 0 110px 0;
        display: flex;
        justify-content: space-between;
        font-family: "Euclid", serif;
        @media (max-width: $md-3) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    // .track__left

    &__left {
        display: flex;
        flex-direction: column;
        @media (max-width: $md-3) {
            align-items: center;
        }
    }

    // .track__title

    &__title {
        font-size: 85px;
        font-weight: 500;
        max-width: 513px;
        margin: 0 0 85px 0;
        @media (max-width: $md-3) {
            text-align: center;
        }
        @media (max-width: $md-4) {
            font-size: 65px;
        }
    }

    // .track__goods

    &__goods {
        display: flex;
        justify-content: center;
        @media (max-width: 900px) {
            background-color: $beigeColor;
            padding: 20px 20px;
            width: 100%;
            height: 100%;
            border-radius: 20px;
        }
        @media (max-width: $md-5) {
          flex-direction: column;
          align-items: center;
        }
    }

    // .track__column

    &__column {
        display: flex;
        flex-direction: column;
        align-items: center;
        // .track__column_1

        &_1 {
        }

        // .track__column_2

        &_2 {
            margin: 35px 0 0 20px;
            @media (max-width: $md-5) {
                margin: 0;
            }
        }
    }

    // .track__good

    &__good {
        margin: 0 0 20px 0;
        height: 226px;
        border-radius: 20px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 35px;
        cursor: pointer;
        text-align: center;
        transition: .5s;
        @media (max-width: $md-5) {
            width: 160%;
        }
        &:hover {
            transition: .5s;
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
        }
    }

    // .track__image

    &__image {
        width: 102px;
        height: 97px;
        object-fit: contain;
        margin: 0 0 25px 0;
    }

    // .track__caption

    &__caption {
        color: $mainColor;
        margin: 0 0 5px 0;
        font-size: 14px;
        font-weight: 500;
    }

    // .track__description

    &__description {
        font-size: 14px;
        font-weight: 400;
        color: rgba(11, 7, 54, .3);
    }

    // .track__right

    &__right { 
        display: flex;
        flex-direction: column;
        @media (max-width: $md-3) {
            display: none;
        }
    }

    // .track__text

    &__text {
        color: rgba(11, 7, 54, .3);
        margin: 70px 0;
        font-weight: 400;
        font-size: 40px;
        max-width: 400px;
    }

    // .track__block

    &__block {
        display: flex;
        flex-direction: column;
    }

    // .track__item

    &__item {
        margin: 0 0 40px 0;
    }

    // .track__ico

    &__ico {

        // .track__ico_1

        &_1 {
        }

        // .track__ico_2

        &_2 {
        }
    }

    // .track__content

    &__content {
        display: flex;
        flex-direction: column;
        
    }

    // .track__name

    &__name {
        font-size: 24px;
        font-weight: 600;
        color: $mainColor;
        margin: 0 0 13px 0;
    }

    // .track__info

    &__info {
        max-width: 300px;
        font-weight: 400;
        font-size: 20px;
        color: rgba(11, 7, 54, .3);
    }

    // .track__background

    &__background {
        position: absolute;
        top: -180px;
        left: 0;
        z-index: -50;
        width: 1200px;
        @media (max-width: 1400px) {
            max-width: 900px;
            top: -80px;
            object-fit: cover;
        }
        @media (max-width: 900px) {
            display: none;
        }
    }
}
