.join {
    width: 90%;
    border-radius: 56px;
    background-color: $blueColor;
    min-height: 643px;
    margin: 110px 5% 100px 5%;
    @media (max-width: $md-4) {
        margin: 90px 5% 60px 5%;
        min-height: 0;
    }
    @media (max-width: $md-5) {
        margin: 60px 5% 40px 5%;
    }
    // .join__container

    &__container {
        width: 100%;
        height: 100%;
    }

    // .join__body

    &__body {
        width: 100%;
        height: 100%;
        padding: 70px 150px 0 150px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 1350px) {
            padding: 70px 100px 0 100px;
        }
        @media (max-width: $md-1) {
            flex-direction: column;
            text-align: center;
            align-items: center;
        }
        @media (max-width: $md-4) {
            padding: 70px 30px;
        }
    }
 
    // .join__content

    &__content {
        display: flex;
        flex-direction: column;
        font-family: "Euclid", sans-serif;
        @media (max-width: $md-1) {
          margin: 0 0 30px 0;
          align-items: center;
        }
    }

    // .join__title

    &__title {
        color: $mainColor;
        font-weight: 500;
        font-size: 68px;
        margin: 0 0 50px 0;
        max-width: 350px;
        @media (max-width: $md-5) {
            font-size: 48px;
        }
    }

    // .join__app

    &__app {
        background-color: $mainColor;
        cursor: pointer;
    }

    // .join__text

    &__text {
        color: rgba(255, 255, 255, 0.507);
    }

    // .join__caption

    &__caption {
        color: #fff;
    }

    // .join__image

    &__image {
        position: relative;
        @media (max-width: $md-4) {
            display: none;
        }
    }
}
