.header {
    width: 100%;
    height: 61px;
    background-color: 0;
    margin: 42px 0 0 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 50;
    // .header__container

    &__container {
        width: 100%;
        height: 100%; 
        padding: 0 90px;
        display: flex;
        align-items: center;
        color: $mainColor;
        @media (max-width: $md-4) {
           justify-content: center;
           padding: 0 20px;
        }
    }

    // .header__logo

    &__logo {
        text-decoration: none;
        color: $mainColor;
        margin: 0 110px 0 0;
        font-size: 24px;
        font-weight: 700;
        font-family: "Euclid", serif;
        cursor: pointer;
        @media (max-width: $md-5) {
            margin: 0 50px 0 0;
        }
    }

    // .header__app

    &__app {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px 29px;
        border: 1px solid $greyColor;
        border-radius: 16px;
        cursor: pointer;   
    }

    // .header__ico

    &__ico {
        width: 22px;
        height: 22px;
        margin: 0 14px 0 0;
        @include bg("contain");
    }
 
    // .header__text

    &__text {
        font-family: "Euclid", serif;
        font-weight: 600;
        font-size: 18px;
    }
}
