*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$md-1: 1200px;
$md-2: 1040px;
$md-3: 992px;
$md-4: 768px;
$md-5: 500px;

$mainColor:  #0B0736;
$yellowColor: #FFD32C;
$blueColor: #D9F4FF; 
$beigeColor: #FEDFB1;
$pinkColor: #FECFD0; 
$greyColor: #E5E5E5;
                 
// Elements

.container {
	max-width: 1040px + 30px;
	margin: 0 auto;
	padding: 0 15px;
}

.circle {
    width: 501px;
    height: 501px;
    border-radius: 50%;
    position: relative;
}

.text-block {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    // .text-block__title

    &__title {
        color: $mainColor;
        font-weight: 500;
        font-size: 68px;
        font-family: "Euclid", serif;
        margin: 0 0 41px 0;
		@media (max-width: $md-4) {
			font-size: 48px;
		}
    }

    // .text-block__text

    &__text {
        font-size: 24px;
        color: $greyColor;
        font-weight: 400;
        font-family: "Euclid", serif;
        margin: 0 0 30px 0;
    }
}

.app-block {
    border-radius: 16px;
    background-color: $blueColor;
    display: flex;
    padding: 20px 15px;
    width: 186px;
    height: 76px;
    text-decoration: none;
	cursor: pointer;
    // .app-block__ico

    &__ico {
        width: 26px;
        height: 100%;
        @include bg("contain");
        margin: 0 15px 0 0;
    }

    // .app-block__right

    &__right {
        display: flex;
        flex-direction: column;
    }

    // .app-block__text

    &__text {
        font-size: 14px;
        font-family: "Euclid", serif;
        color: rgba(19, 15, 73, .3);
    }

    // .app-block__caption

    &__caption {
        font-size: 19px;
        color: $mainColor;
        font-family: "Euclid", serif;
    }
}
